import React, { lazy, Suspense, useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';

import axios from '../../axios';

const StudentListUI = lazy( () => import('./StudentListUI') );

const StudentList = () => {

    const history = useHistory();
    const [ Students, setStudents ] = useState([]);

    useEffect(
        () => {

            setInterval(() => {
                GetStudents();
            }, 500);
            
        }, []
        );
        
        const GetStudents = () => {
            
            axios.get('/students/list').then(
                ( res ) => {
    
                    setStudents(
                        res.data
                    );
    
                }
            ).catch(
                ( err ) => {
    
                    console.log( err );
    
                }
            )

    }

    const NewStudent = () => {
        
        history.push('/students/entry=new');

    }

    const EditStudent = ( stdID ) => {
        
        history.push('/students/entry=edit&&registration=' + stdID);

    }

    return (
        <Suspense fallback={ <div>Loading....</div> }>
            <StudentListUI
                Students={ Students }
                NewStudent={ NewStudent }

                EditStudent={ EditStudent }
            />
        </Suspense>
    )

}

export default StudentList;